<template>
    <div>
        <b-navbar toggleable="md" type="light" variant="default" class="bg-navbar">
            <div id="wrap-navbar">
                <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
                <b-navbar-brand href="#">
                    <a class="navbar-brand mf-navbar-brand" href="#">
                        <img src="../assets/images/logo_movefast.png" class="mf-logo">
                    </a>
                </b-navbar-brand>
                <b-collapse is-nav id="nav_collapse">
                    <!-- Right aligned nav items -->
                    <b-navbar-nav class="ml-auto">
                        <b-nav-item-dropdown right>
                            <!-- Using button-content slot -->
                            <template slot="button-content">
                                <img :src="getAvatar" class="img-admin">{{ currentAdmin.displayName }}
                            </template>
                            <b-dropdown-item @click="logout"><i class="fa fa-sign-out set-icon"></i>Logout</b-dropdown-item>
                        </b-nav-item-dropdown>
                    </b-navbar-nav>
                </b-collapse>
            </div>
        </b-navbar>
        <!-- <nav class="navbar navbar-expand-lg navbar-light bg-navbar">
            <div id="wrap-navbar">
                <a class="navbar-toggler mf-navbar-toggler" type="button" data-toggle="dropdown"  aria-expanded="false" aria-label="Toggle navigation">
                    <img src="../assets/images/profile_admin.png" class="m-img-admin">
                </a>

                <div class="dropdown-menu mf-dropdown-menu">
                    <a class="dropdown-item" href="#"><i class="fa fa-sign-out set-icon"></i>Logout</a>
                </div>

                <div class="wrap-back">
                    <button type="button" class="bt-back">
                        <i class="fa fa-chevron-left"></i>
                    </button>
                </div>

                <a class="navbar-brand mf-navbar-brand" href="#"><img src="../assets/images/logo_movefast.png" class="mf-logo"></a>

                <div class="collapse navbar-collapse" id="navbarTogglerDemo03">
                    <ul class="navbar-nav ml-auto mt-2 mt-lg-0">
                        <li class="nav-item dropdown">
                            <a class="nav-link dropdown-toggle mf-nav-link" href="#" id="navbardrop" data-toggle="dropdown">
                                <img :src="getAvatar" class="img-admin">{{ currentAdmin.displayName }}
                            </a>
                            <div class="dropdown-menu mf-dropdown-menu">
                                <a class="dropdown-item" href="#"><i class="fa fa-sign-out set-icon"></i>Logout</a>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>-->
    </div>
</template>
<script>
  /* eslint-disable */
    import { mapState, mapActions } from 'vuex'
    export default {
        name: 'navbar',
        computed: {
            ...mapState({
                currentAdmin: state => state.admins.currentAdmin
            }),
            getDisplayName (){
                if (typeof(this.currentAdmin.displayName) != undefined){
                    if (this.currentAdmin.displayName || this.currentAdmin.displayName != ""){
                        return this.currentAdmin.email
                    }
                }
                return 'no name'
            },
            getAvatar (){
                if (typeof(this.currentAdmin.pictureUrl) != undefined){
                    if (this.currentAdmin.pictureUrl || this.currentAdmin.pictureUrl != ""){
                        return this.currentAdmin.pictureUrl
                    }
                }
                return require('../assets/images/profile_img.png')
            },
        },
        methods: {
            ...mapActions({

            }),
            logout() {
                this.$store.dispatch("admins/logout")
                .then((result) => {
                    console.log(result)
                    // this.$router.push("/")
                    this.$router.replace({ name: "login" })
                })
                .catch((error) => {
                    this.$notify({
                        group: 'login',
                        type: 'error',
                        text: error.message
                    });
                    console.error(error.message)
                })
            }
        }
    }
</script>
<style lang="css">
    a.dropdown-item {
        outline: none;
    }
    a.dropdown-item:focus {
        background-color: #ffffff;
    }
    a.dropdown-item:hover {
        background-color: #f8f9fa;
    }
</style>



