<template>
    <div> 
        <div class="container-fluid">
            <form class="form-signin" @submit.prevent="onSubmit">
                <div class="row">
                    <div class="col-sm-4 text-center wrap-singin-logo d-none d-sm-block">
                        <img src="../assets/images/logo_movefast_login.png" class="mf-logo-singin">
                    </div>
                    <div class="col-sm-8 col-xs-12 wrap-signin">
                        <img src="../assets/images/movefast_icon.png" class="mf-logo-singin d-block d-sm-none">
                        <h2 class="text-center">เข้าสู่ระบบเพื่อเริ่มต้นใช้งาน</h2>
                        <div class="row justify-content-center wrap-input">
                            <div class="col-sm-8 col-xs-12">
                                <div class="form-group">
                                    <label for="email">E-mail</label>
                                    <input type="text" class="form-control" id="email" name="email" v-model="email" v-validate="'required|email'">
                                    <label class="text-error text-light" v-if="errors.has('email')">{{ errors.first('email') }}</label>
                                </div>
                                <div class="form-group">
                                    <label for="password">Password</label>
                                    <input type="password" class="form-control" id="password" name="password" v-model="password" v-validate="'required|min:4'">
                                    <label class="text-error text-light" v-if="errors.has('password')">{{ errors.first('password') }}</label>
                                </div>
                                <!-- <div class="row width100">
                                    <div class="col-7">
                                        <div class="form-check">
                                            <label class="form-check-label">
                                                <input type="checkbox" class="form-check-input" value=""><span class="text-small">Remember me</span>
                                            </label>
                                        </div>
                                    </div>
                                    <div class="col-5 text-right noPadding">
                                        <a href="#" class="text-small mf-link">ลืมรหัสผ่าน ?</a>
                                    </div>
                                </div> -->
                            </div>
                        </div>
                        <div class="row justify-content-center">
                            <div class="col-8">
                                <button type="submit" class="btn bt-singin btn-lg btn-block has-spinner">เข้าสู่ระบบ</button>
                            </div>
                        </div>
                    </div>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
/* eslint-disable */
import { mapState, mapMutations, mapActions } from 'vuex'
export default {
    name: 'login',
    data() {
        return {
            email: "",
            password: ""
        }
    },
    mounted:function(){
        if(Object.keys(this.currentAdmin).length === 0 && this.currentAdmin.constructor === Object) {
            if (localStorage.getItem('data')){
                //get admin data
                let email = localStorage.getItem('data')
                this.$store.dispatch("admins/restoreCurrentAdmin", email)
                .then((result) => {
                    console.log(result);
                    // this.$router.push("/")
                    this.$router.replace({ name: "chat" });
                })
                .catch((error) => {
                    console.error(error.message)
                    this.$router.replace({ name: "login" });
                })
            }else{
                this.$router.replace({ name: "login" });
            }
        }else{
            this.$router.replace({ name: "chat" });
        }
    },
    methods:{
        ...mapActions({
            
        }),
        onSubmit() {
            this.$validator.validateAll()
            .then((result) => {
                if(!result){
                    // alert('error');
                    return;
                }
                // alert('success');
                this.$store.dispatch("admins/login", {
                    email: this.email,
                    password: this.password
                })
                .then((result) => {
                    console.log(result);
                    // this.$router.push("/")
                    this.$router.replace({ name: "chat" });
                })
                .catch((error) => {
                    this.$notify({
                        group: 'login',
                        type: 'error',
                        text: error.message
                    });
                    console.error(error.message)
                })
            })
            .catch(() => {
            });
        }        
    },
    components: {

    },
    computed: {
        ...mapState({
            currentAdmin: state => state.admins.currentAdmin
        }),
    }
}
</script>
<style>
    @import '../assets/css/login.css';
</style>



