/* eslint-disable */
import axios from 'axios'
const state = {
  all: {}
}
const mutations = {
  SET_ALL (state, { user }) {
      state.all = {...state.all, [user.id]: user.data() }
  }
}
const actions = {
  async loadAll ({ commit, rootState }) {
    let userRef = rootState.db.collection('users')
    return await userRef.onSnapshot(querySnapshot => {
      querySnapshot.forEach(user => commit('SET_ALL', { user }))
    })
  }
}
export default { 
  namespaced: true, state, mutations, actions
}


