<template>
  <div> 
    <NavBar/>
    <div class="container mf-container">
        <div class="row" id="wrap-row">
            <div class="mf-col-3" id="all-message">
                <!-- Search-->
                <div id="search">
                    <input type="text" placeholder="search">
                </div>
                <!-- Status -->
                <div id="chat-status">
                    <ul>
                        <li class="active"><a>All</a></li>
                        <li><a>Read</a></li>
                        <li><a>Unread</a></li>
                        <li class="ml-auto"><img src="../assets/images/icon-line.png" id="img-icon"></li>
                    </ul>
                </div>
                <!-- List message-->
                <UserContainer/>
            </div>
            <div class="mf-col-5" id="wrap-message">
                <ConversationContainer/>
            </div>
            <div class="mf-col-4" id="wrap-profile-user">
                <ProfileContainer/>
            </div>
        </div>
    </div>
  </div> 
</template>
<script>
/* eslint-disable */
// import 'bootstrap/dist/css/bootstrap.css'
// import 'bootstrap-vue/dist/bootstrap-vue.css'
import NavBar from './NavBar.vue'
import ConversationContainer from './ConversationContainer.vue'
import UserContainer from './UserContainer.vue'
import ProfileContainer from './ProfileContainer.vue'
import { mapState, mapActions } from 'vuex'
export default {
    name: 'chat',
    mounted:async function(){
        if (Object.keys(this.currentAdmin).length === 0 && this.currentAdmin.constructor === Object) {
            if (localStorage.getItem('data')){
                //get admin data
                let email = localStorage.getItem('data')
                this.$store.dispatch("admins/restoreCurrentAdmin", email)
                .then((result) => {
                    console.log(result);
                    // this.$router.push("/")
                    this.$router.replace({ name: "chat" });
                })
                .catch((error) => {
                    console.error(error.message)
                    this.$router.replace({ name: "login" });
                })
            }else{
                this.$router.replace({ name: "login" });
            }
        }else{
            this.$router.replace({ name: "chat" });
        }
        await this.getChannelAll()
        await this.getUserAll()
        await this.getConversationAll()
    },
    methods:{
        ...mapActions({
            getChannelAll: 'channels/loadAll',
            getUserAll: 'users/loadAll',
            getConversationAll: 'conversations/loadAll'
        }),
    },
    components: {
        NavBar,
        UserContainer,
        ConversationContainer,
        ProfileContainer
    },
    computed: {
        ...mapState({
            currentAdmin: state => state.admins.currentAdmin,
        }),
    }
}
</script>
<style lang="css">
    @import 'https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700';
    @import 'https://fonts.googleapis.com/css?family=Prompt:300,400,500';
    @import 'https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css';
    @import '../assets/css/main_3.css';
</style>



