    <div class="profile-user">
        <img :src="getAvatar">
        <div class="profile-user-status active"></div>
        <span>{{ getDisplayName }}</span><br/>
        <span>ID : amyamy_moon</span><br/>
        <span class="province">Bangkok</span>
    </div>
    <div class="older-order">
        <div class="older-order-title">
            Last Order
        </div>
        <div class="older-order-content">
            <ul>
                <li>
                    <div class="row row-order-content">
                        <div class="col-6 text-left no-pad">
                            BO00010518-00041
                        </div>
                        <div class="col-6 text-right no-pad">
                            <div class="float-right"><span>11:32</span></div>
                            <div class="float-right"><span>1 May 2018 | &nbsp;</span></div>
                        </div>
                    </div>
                    <div class="row row-order-content">
                        <div class="col-6 text-left no-pad">
                            <span>Payment by : </span><span class="badge badge-Kbank">KBANK</span>
                        </div>
                        <div class="col-6 text-right no-pad">
                            <span>Status : </span><span class="badge badge-delivered">Delivered</span>
                        </div>
                    </div>
                    <div class="row row-order-content">
                        <div class="col-12 text-left no-pad">
                            <div class="float-left">
                                <img src="../assets/images/img-product-1.png" class="img-product">
                            </div>
                            <div class="order-product float-left">
                                <div class="product-name float-left">
                                    Liv White Diamond Cream&nbsp;&nbsp;
                                </div>
                                <div class="order-count float-left">
                                    x2 free1
                                </div>
                            </div>
                            <div class="float-right total-price">
                                1,980.-
                            </div>
                        </div>
                    </div>
                </li>
                <li>
                    <div class="row row-order-content">
                        <div class="col-6 text-left no-pad">
                            BO00300418-00032
                        </div>
                        <div class="col-6 text-right no-pad">
                            <div class="float-right"><span> 11:32</span></div>
                            <div class="float-right"><span>30 Apr 2018 | &nbsp;</span></div>
                        </div>
                    </div>
                    <div class="row row-order-content">
                        <div class="col-6 text-left no-pad">
                            <span>Payment by : </span><span class="badge badge-scb">SCB</span>
                        </div>
                        <div class="col-6 text-right no-pad">
                            <span>Status : </span><span class="badge badge-success">Success</span>
                        </div>
                    </div>
                    <div class="row row-order-content">
                        <div class="col-12 text-left no-pad">
                            <div class="float-left">
                                <img src="../assets/images/img-product-1.png" class="img-product">
                            </div>
                            <div class="order-product float-left">
                                <div class="product-name float-left">
                                    Liv White Diamond Cream&nbsp;
                                </div>
                                <div class="order-count float-left">
                                    x1
                                </div>
                            </div>
                            <div class="float-right total-price">
                                990.-
                            </div>
                        </div>
                    </div>
                    <div class="row row-order-content">
                        <div class="col-12 text-left no-pad">
                            <div class="float-left">
                                <img src="../assets/images/img-product-2.png" class="img-product">
                            </div>
                            <div class="order-product float-left">
                                <div class="product-name float-left">
                                    Liv White Diamond DD #1&nbsp;&nbsp;
                                </div>
                                <div class="order-count float-left">
                                    x1
                                </div>
                            </div>
                            <div class="float-right total-price">
                                690.-
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
    <div id="block-user">
        <div class="row justify-content-md-center">
            <div class="col-sm-9">
                <button type="button" class="btn bt-primary btn-md btn-block">Save User</button>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
  /* eslint-disable */
    import { mapState, mapActions } from 'vuex'
    export default {
        name: 'profileContainer',
        computed: {
            ...mapState({
                users: state => state.users.all,
                currentConversation: state => state.conversations.currentConversation
            }),
            getDisplayName (){
                if (this.currentConversation){
                    let user = this.users[this.currentConversation.userId];
                    if (user.displayName || user.displayName != ""){
                        return user.displayName
                    }
                }
                return 'no name'
            },
            getAvatar (){
                if (this.currentConversation){
                    let user = this.users[this.currentConversation.userId];
                    if (typeof(user.pictureUrl) != undefined){
                        if (user.pictureUrl || user.pictureUrl != ""){
                            return user.pictureUrl
                        }
                    }
                    
                }
                return require('../assets/images/profile_img.png')
            },
        },
        methods: {
            ...mapActions({

            })
        }
    }
</script>
<style scoped>

</style>



