<template>
  <div>
    <div id="chat-content" ref="chat-content">
        <ul id="chat">
          <Message 
            v-for="message in messages" 
            :message="message" 
            :key="message.id" 
          />
        </ul>
    </div>
    <div id="chat-write" v-if="getMessageSize > 0">
      <div class="add-file float-left">
          <a href="#">
              <img src="../assets/images/icon-add-file.png" class="img-add-file">
          </a>
          <a href="#">
              <img src="../assets/images/icon-add-emo.png" class="img-add-file">
          </a>
      </div>
      <textarea v-model="newMessageText" class="form-control float-left" rows="3" id="comment" placeholder="Write a message..."></textarea>
      <div class="add-file sent-chat float-left">
          <button type="button" class="bt-sent-chat" @click="send()">
              <img src="../assets/images/icon-add-sent.png" class="img-add-file">
          </button>
      </div>
    </div>
    
  </div>
</template>
<script>
  
</script>
<style scoped>
</style>

<script>
  /* eslint-disable */
  import Message from './Message.vue'
  import { mapState, mapGetters, mapActions } from 'vuex'
  export default {
    components: {
      Message
    },
    // created: function() {
    //   this.scrollToEnd()
    // },
    updated: function() {
      this.scrollToEnd()
    },
    computed: {
      ...mapState({
        messages: state => state.conversations.messages,
      }),
      getMessageSize () {
        return Object.keys(this.messages).length
      }
    },
    // watch: {
    //   // whenever question changes, this function will run
    //   messages: function (newMessages, oldMessages) {
    //     this.scrollToEnd()
    //   }
    // },
    methods: {
      ...mapActions({
        // send: 'conversations/sendMessage',
      }),
      send() {
        // console.log(this.newMessageText)
        this.$store.dispatch("conversations/sendMessage", this.newMessageText)
        this.newMessageText = ""
      },
      scrollToEnd(){
        let container = this.$refs["chat-content"];
        container.scrollTop = container.scrollHeight
      }
    },
    data () {
        return {
          newMessageText: ''
        }
    }
  }
</script>
<style scoped>

</style>




