/* eslint-disable */
const state = {
  currentAdmin: {}
}
const mutations = {
  CLEAR_CURRENT_ADMIN (state) {
    state.currentAdmin = {}
  },
  UPDATE_CURRENT_ADMIN (state, currentAdmin) {
    state.currentAdmin = currentAdmin
  },
}
const actions = {
  logout ({ commit, rootState}) {
    return new Promise((resolve, reject) => {
      commit('CLEAR_CURRENT_ADMIN')
      localStorage.removeItem("data")
      resolve('Logout success')
    })
  },
  login ({ commit, rootState}, {email, password}) {
    return new Promise((resolve, reject) => {
      let adminRef = rootState.db.collection('admins')
      adminRef.where('email', '==', email)
        .get()
        .then(function(querySnapshot) {
          let admin
          querySnapshot.forEach(function(doc) {
            admin = { ...doc.data(), docId: doc.id }
            return
          });
          if (admin){
            if (admin.password === password){
              commit('UPDATE_CURRENT_ADMIN', admin)
              localStorage.setItem('data', admin.email);
              resolve('Login success')
            }else{
              reject(new Error("The username and / or password is incorrect"))
              console.log("The username and / or password is incorrect");
            }
          }else{
            reject(new Error("The username and / or password is incorrect"))
          }
        })
        .catch(function(error) {
          reject(error)
        })
    })
  },
  restoreCurrentAdmin ({commit, rootState}, email){
    let adminRef = rootState.db.collection('admins')
    return new Promise((resolve, reject) => {
      adminRef.where('email', '==', email)
        .get()
        .then(function(querySnapshot) {
          let admin
          querySnapshot.forEach(function(doc) {
            admin = { ...doc.data(), docId: doc.id }
            return
          });
          if (admin){
            commit('UPDATE_CURRENT_ADMIN', admin)
            // localStorage.setItem('data', admin[0].email)
            resolve('Restore currentAdmin success')
          }else{
            localStorage.removeItem('data')
            reject(new Error("Not found admin as given email"))
          }
        })
        .catch(function(error) {
          reject(error)
        })
    })
  },
  checkLogin ({ commit }){
    return (Object.keys(this.currentAdmin).length === 0 && this.currentAdmin.constructor === Object)
  }
}
export default { 
  namespaced: true, state, mutations, actions
}


