/* eslint-disable */
import axios from 'axios'
const state = {
  all: {},
  messages: {},
  currentConversation: {}
}
const mutations = {
  SET_ALL (state, { conversation }) {
    state.all = {...state.all, [conversation.id]: conversation.data() }
  },
  UPDATE_CURRENT_CONVERSATION (state, currentConversation) {
    state.currentConversation = currentConversation
  },
  LOAD_MESSAGES (state, { message }) {
    state.messages = {...state.messages, [message.id]: message.data() }
  },
  CLEAR_MESSAGES (state){
    state.messages = {}
  },
  ADD_MESSAGE (state, { conversationId, message }) {
      if (!state.allMsgIds.includes(message.id)) {
          state.all[conversationId].messages.push(message)
          state.allMsgIds.push(message.id)
      }
  }
}
 
const actions = { 
  async loadAll ({ commit, rootState }) {
    let conversationRef = rootState.db.collection('conversations').orderBy("updatedAt", "desc")
    return await conversationRef.onSnapshot(querySnapshot => {
      querySnapshot.forEach(conversation => commit('SET_ALL', { conversation }))
    })
  },
  async loadMessages ({ commit, rootState }, conversationId) {
    commit('CLEAR_MESSAGES')
    commit('UPDATE_CURRENT_CONVERSATION', {...rootState.conversations.all[conversationId], docId: conversationId})
    let messsageRef = rootState.db.collection('conversations').doc(conversationId).collection('messages').orderBy("updatedAt")
    return await messsageRef.onSnapshot(querySnapshot => {
      querySnapshot.forEach(message => commit('LOAD_MESSAGES', { message }))
    })
  },
  async sendMessage ({ commit, rootState, dispatch }, text) {
    if (text.trim() === ""){
      return
    }
    let conversationId = rootState.conversations.currentConversation.docId
    let adminId = rootState.admins.currentAdmin.docId
    let messageRef = rootState.db.collection('conversations').doc(conversationId).collection('messages')
     //create new message
    let ref = await messageRef.add({
        senderId: adminId,
        senderType: 'admin',
        text: text,
        isRead: false,
        createdAt: Date.now(),
        updatedAt: Date.now()
    });
    dispatch('postMessage', text);
  },
  postMessage ({ commit, rootState }, text) {
    let userId = rootState.conversations.currentConversation.userId
    let user = rootState.users.all[userId]
    let channelId = rootState.conversations.currentConversation.channelId
    let channel = rootState.channels.all[channelId]
    if (channel.provider === "line"){
      let url = '/api/v1/lines/'+channel.id+'/send'
      axios.post(url, {
          userId: user.id,
          msg: text,
          type:'text'
      })
      .then(function (response) {
        // handle success
        console.log(response);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
    }
    return
  }
}
export default { namespaced: true, state, mutations, actions }


