/* eslint-disable */
const state = {
    all: {}
}
const mutations = {
    SET_ALL (state, { channel }) {
        state.all = {...state.all, [channel.id]: channel.data() }
    },
}
const actions = {
    async loadAll ({ commit, rootState }) {
        let channelRef = rootState.db.collection('channels')
        return await channelRef.onSnapshot( querySnapshot => {
            querySnapshot.forEach(channel => commit('SET_ALL', { channel }))
        })
    },
}
export default { 
  namespaced: true, state, mutations, actions
}


