<template>

    <li :class="getPosition">
      <div class="img-profile-small">
          <a data-toggle="modal" data-target=".profileModal"><img :src="getAvatar"></a>
      </div>
      <div class="triangle"></div>
      <div class="message">{{ message.text }}</div>
      <div class="sent-time">{{ message.createdAt | moment("lll") }}</div>
    </li>

</template>
<script>
  /* eslint-disable */
  import { mapState, mapGetters, mapActions } from 'vuex'
  export default {
    name: 'Message',
    // created(){
    //   console.log(message)
    // },
    props: {
      message: {
        //type: Object
      }
    },
    computed: {
      ...mapState({
        users: state => state.users.all,
      }),
      getPosition () {
        // console.log(this.message)
        if (this.message.senderType != "user") {
          return 'me'
        } else {
          return 'you'
        }
      },
      getAvatar (){  
        if (this.message.senderType === "user") {
          let user = this.users[this.message.senderId];
          if (typeof(user.pictureUrl) != undefined){
            if (user.pictureUrl || user.pictureUrl != "" && user.pictureUrl != undefined){
              return user.pictureUrl
            }
          }
        }
        return require('../assets/images/profile_img.png')
      },
    }
  }
</script>
<style scoped>
.my.message {
  text-align: right;
}
.message-container {
  border: 1px solid silver;
}
</style>


